<template>
  <div>

    <el-container>
      <el-header>
        <pageTop title="作者信息"
                 style="position:absolute;background:transparent"></pageTop>
        <div class="img">
          <img :src="img"
               alt="">
        </div>

      </el-header>

      <el-container>

        <el-main>
          <div class="btn">
            <el-tabs v-model="activeName"
                     @tab-click="handleClick">
              <el-tab-pane name="first">
                <span slot="label"> <i class="el-icon-s-home"></i> 主页</span>
                <blogList :data="blogList"
                          :infiniteScroll="true"
                          @infiniteFn="getUser"
                          :infiniteDisable="infiniteDisable">

                  <div slot="input">
                    <label for="">标题：</label>

                    <el-input style="display:inline-block;width:400px"
                              v-model="blogTitle"
                              placeholder="博客标题"></el-input>
                    <el-button icon="el-icon-search"
                               @click="searchParam"
                               plain></el-button>
                  </div>
                </blogList>
              </el-tab-pane>
              <el-tab-pane name="second"> <span slot="label"> <i class="el-icon-star-on"></i> 收藏</span></el-tab-pane>
              <el-tab-pane name="third"><span slot="label"> <i class="el-icon-chat-line-round"></i> 评论</span></el-tab-pane>

            </el-tabs>
          </div>

        </el-main>
        <el-aside width="400px">
          <div class="number">
            <div class="word">
              <span>关注数</span>
              <span> 获赞数</span>
              <span>阅读数</span>
            </div>
            <div>
              <span>100</span>
              <span>1451</span>
              <span>332万</span>
            </div>

          </div>
          <el-card shadow="never">
            <div>
              <span class="title">作者</span>
              <span class="content">{{userInfo.name}}</span>
            </div>
            <div>
              <span class="title">电话</span>
              <span class="content">{{userInfo.phoneNum}}</span>
            </div>
            <div>
              <span class="title">邮箱</span>
              <span class="content">{{userInfo.email}}</span>
            </div>
            <div>
              <span class="title">简介</span>
              <span class="content">{{userInfo.summary}}</span>
            </div>
          </el-card>

          <!-- <el-card shadow="hover">
            <div>
              <span class="title">简介</span>
              <span class="content"> {{userInfo.summary}}{{userInfo}}</span>
            </div>
          </el-card>

          <el-card shadow="hover">
            <div>
              <span class="title">个人认证</span>
              <span class="content"> 著名小说家，散文家。著有《xxx的一生》，影响几百万青少年成长，启迪人们认识自己，与自己和解。</span>
            </div>
          </el-card> -->

        </el-aside>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import top from '../components/top'
import con3 from "@/assets/images/topSample2.jpg";
import blogList from '@/components/blogList'
import systemBlogType from '../components/systemBlogType/systemBlogType'
export default {
  data () {
    return {
      userID: '',
      api_token: '',
      img: con3,
      activeName: 'first',
      blogList: [],
      blogTitle: '',
      infiniteDisable: false,
      index: 1,
      pagecount: 10,
      userInfo: {

      }

    }
  },
  components: {
    blogList,
    systemBlogType,
    pageTop: top
  },
  mounted () {
    this.userID = this.$route.query.userOnlyId
    if (window.sessionStorage.getItem('apiToken')) {
      this.api_token = window.sessionStorage.getItem('apiToken')
    } else {
      this.$router.push('login')
    }
    // this.getUser()
  },

  methods: {
    getUser (data, callback) {
      this.index++
      this.$API.getUser({ api_token: this.api_token, userOnlyId: this.userID, index: this.index, pagecount: this.pagecount, blogTitle: this.blogTitle }).then(
        res => {

          if (this.index * this.pagecount >= res.data.blogNum) {
            callback(true, true)
          }
          this.blogList = this.blogList.concat(res.data.data.blogList);
          this.userInfo = res.data.data.userInfo[0]

          callback(true, false)
        }
      ).catch(err => {
        console.log(err)
      })
    },
    searchParam (parm) {
      // this.$message.warning("此功能未做")

      // this.$API.getUser({ api_token: this.api_token, userOnlyId: this.userID }).then(
      //   res => {
      //     console.log(res)
      //   }
      // ).catch(err => {
      //   console.log(err)
      // })
    },
    handleClick () {

    }
  }
}
</script>

<style lang="less" scoped>
.el-container {
  // background: #e4e7ed;
  .el-header {
    height: 200px !important;
    .img {
      img {
        height: 200px;
        width: 100%;
        object-fit: cover;
      }
    }
    .btn {
      height: 100px;
    }
  }
  .el-main {
    padding: 0px 20px;
    .el-tabs__header {
      margin: 0px;
    }
  }
}

.el-aside {
  //background: #eeeeee;
  .number {
    width: 100%;
    background: #ffff;
    //text-align: center;
    height: 40px;
    padding: 10px 0px;
    .word {
      color: #909399;
    }
    span {
      width: 60px;
      display: inline-block;
      padding: 5px 10px;
    }
  }

  .el-card {
    width: 100%;
    padding: 10px 0px;
    border: none;

    .title {
      display: inline-block;
      width: 50px;
      vertical-align: top;
    }
    .content {
      display: inline-block;
      font-size: 12px;
      width: 160px;
    }
  }
}
</style>


